header .container-fluid { background-color: #f3af9d; }

.page-header { margin: 0 auto; padding: 0 10px; display: flex; flex-direction: row; justify-content: space-between; max-width: 960px; height: 78px; background-color: #fdfdfd; }

@media only screen and (max-width: 992px) { .page-header { padding: 0 25px; } }

.logo { padding-top: 3px; font-family: "BebasBold", sans-serif; font-size: 46px; font-weight: bold; color: #f3af9d; text-transform: uppercase; line-height: 78px; letter-spacing: 0.04em; }

.logo strong { color: #e8603c; }

.logo--inline { display: inline-block; }

.page-name { margin: 0 auto; padding: 0 10px; display: flex; flex-direction: row; justify-content: space-between; height: 60px; max-width: 960px; background-color: white; }

.page-name h2 { padding-top: 1px; padding-left: 18px; width: 100%; font-family: "BebasBold", sans-serif; font-size: 30px; font-weight: bold; color: white; background-color: #e8603c; text-align: left; text-transform: uppercase; line-height: 60px; letter-spacing: 0.03em; vertical-align: middle; }

/*RESPONSIVE NAVBAR*/
.nav { padding-bottom: 30px; }

.nav__main { padding: 0; }

.nav__main ul { list-style-type: none; margin: 0; padding: 0; text-align: center; }

.nav__main li { display: inline-block; }

.nav__main li:last-child { border-right: none; }

.nav__main .current a { color: #e8603c; }

.nav__main a { display: block; padding: 5px 0px 0px 22px; font-family: "BebasBold", sans-serif; font-size: 20px; font-weight: bold; color: #b9b9b9; text-decoration: none; text-transform: uppercase; line-height: 78px; vertical-align: middle; transition: 0.25s; letter-spacing: 0.04em; }

.nav__main a:hover { color: #e8603c; text-decoration: underline; }

@media only screen and (max-width: 720px) { .nav__main { display: none !important; } }

.nav__trigger { display: none; text-align: center; }

.nav__trigger span { display: inline-block; padding: 0px 30px; font-family: "BebasBold", sans-serif; font-size: 24px; font-weight: bold; color: #e8603c; text-transform: uppercase; line-height: 77px; vertical-align: middle; cursor: pointer; text-transform: uppercase; transition: 0.5s; }

.nav__trigger span:after { display: inline-block; margin-left: 10px; width: 20px; height: 10px; font-family: "FontAwesome"; content: "\f0c9"; }

.nav__trigger span:hover { background-color: #e6002d; }

.nav__trigger .open { margin-top: -2px; background-color: #e6002d; padding: 0px 10px 0px 80px; transition: 0.5s; }

@media only screen and (max-width: 720px) { .nav__trigger { display: block !important; } }

.nav__mobile { position: relative; display: none; z-index: 99; }

.nav__mobile ul { display: none; list-style-type: none; position: absolute; left: 0; right: 0; margin-left: auto; margin-right: auto; text-align: center; background-color: #e6002d; }

.nav__mobile li { display: block; padding: 5px 0; margin: 0 5px; border-bottom: solid 1px #cc0028; }

.nav__mobile li:last-child { border-bottom: none; }

.nav__mobile a { display: block; padding: 10px 30px; color: #b9b9b9; font-family: "BebasBold", sans-serif; font-size: 20px; font-weight: bold; color: #b9b9b9; text-decoration: none; text-transform: uppercase; }

.nav__mobile a:hover { background-color: #e6002d; color: #fff; }

@media only screen and (max-width: 720px) { .nav__mobile { display: block !important; } }

.page-content { margin: 0 auto; max-width: 960px; padding: 24px 10px 50px 10px; display: grid; grid-template-columns: 9fr 3fr; }

@media only screen and (max-width: 960px) { .page-content { display: block; grid-template-columns: none; padding-bottom: 0; max-width: 100%; } }

.post { padding-bottom: 47px; margin-bottom: 56px; position: relative; border: solid; border-right: 0; border-left: 0; border-top: 0; border-bottom-width: 14px; border-image: url(../img/sep-line.png) 0 0 14 0 repeat; }

@media only screen and (max-width: 1024px) { .post { margin: 0 auto 50px auto; max-width: 670px; } }

.post__icon { position: absolute; left: -85px; }

@media only screen and (max-width: 1200px) { .post__icon { display: none; } }

.post__media { position: relative; max-width: 670px; }

.post__meta { padding-top: 20px; float: left; width: 147px; }

@media only screen and (max-width: 640px) { .post__meta { float: none; display: flex; flex-direction: row; justify-content: space-between; width: 100%; } }

.post__summary { padding-top: 20px; overflow: hidden; }

.slider__arrow-prev { position: absolute; top: 50%; transform: translateY(-50%); left: 18px; }

.slider__arrow-prev i { font-size: 56px; color: #fff; }

.slider__arrow-next { position: absolute; top: 50%; transform: translateY(-50%); right: 18px; }

.slider__arrow-next i { font-size: 52px; color: #fff; }

.slider-item img { width: 100%; }

.data { padding: 2px 0 0 0; margin: 0 0 8px 0; }

@media only screen and (max-width: 640px) { .data { text-align: center; } }

.data__label { padding: 0; margin: 0; font-family: "BebasBold", sans-serif; font-size: 16px; font-weight: bold; color: #e8603c; text-transform: uppercase; text-decoration: none; }

.data__label:hover { text-decoration: underline; }

.data__value { padding-top: 1px; font-family: "HelveticaNeueRoman", sans-serif; font-size: 12px; color: #bababa; }

@media only screen and (max-width: 640px) { .data__value { text-align: center; } }

.headline a { font-family: "BebasBold", sans-serif; font-size: 22px; color: #e8603c; text-align: left; text-transform: uppercase; text-decoration: none; letter-spacing: 0.035em; }

.headline a:hover { text-decoration: underline; }

.textual { padding-top: 12px; }

.textual p { max-width: 505px; font-family: "HelveticaNeueRoman", sans-serif; font-size: 13px; font-weight: 400; font-style: normal; color: #414141; text-align: left; line-height: 1.4em; padding-bottom: 18px; }

.textual p:last-of-type { padding-bottom: 0px; }

.textual__url { padding-top: 5px; padding-bottom: 20px; font-family: "HelveticaNeueRoman", sans-serif; font-size: 14px; font-weight: 400; font-style: normal; color: #a0a0a0; }

.textual--sidebar-text p { padding: 0; color: #464646; }

.quote { padding: 20px 50px 20px 50px; display: flex; flex-direction: row; justify-content: space-between; background-color: #f5f5f5; }

@media only screen and (max-width: 640px) { .quote { display: flex; flex-direction: column; justify-content: flex-start; padding: 20px; } }

.quote__mark { padding-left: 7px; padding-right: 20px; font-family: Georgia, sans-serif; font-size: 72px; color: #e8603c; line-height: 1em; }

.quote__text { font-family: "HelveticaNeueItalic", sans-serif; font-size: 14px; font-weight: 300; font-style: italic; color: #414141; text-align: left; line-height: 1.5em; }

.pagination-list { margin-top: 50px; }

.pagination-list li { display: inline-block; line-height: 26px; background-color: #f3af9d; vertical-align: middle; transition: 0.2s; }

.pagination-list li:hover { background-color: #e8603c; }

.pagination-list a { padding: 0 9px; text-decoration: none; font-family: "BebasRegular", sans-serif; font-size: 14px; font-weight: normal; font-style: normal; color: #fff; line-height: 26px; }

.pagination-list .is-current { background-color: #e8603c; }

@media only screen and (max-width: 960px) { .pagination-list { display: flex; flex-direction: row; justify-content: center; padding-bottom: 50px; }
  .pagination-list li { margin: 0 5px; } }

.sidebar { padding: 0 5px; }

@media only screen and (max-width: 960px) { .sidebar { max-width: 670px; margin: 0 auto; } }

.sidebar-section { padding-bottom: 30px; }

.sidebar-section__header { display: flex; flex-direction: row; background: url(../img/sep-line.png); height: 18px; }

.sidebar-section .h4 { padding-right: 10px; background-color: white; font-family: "BebasBold", sans-serif; font-size: 20px; font-weight: 700; font-style: bold; color: #e8603c; text-transform: uppercase; text-decoration: none; }

.sidebar-section .h4:hover { text-decoration: underline; }

.sidebar-section p, .sidebar-section a { font-family: "HelveticaNeueRoman", sans-serif; font-size: 13px; font-weight: 400; font-style: normal; color: #464646; line-height: 1.25em; }

.sidebar-section a { text-decoration: none; }

.sidebar-section a:hover { text-decoration: underline; }

@media only screen and (max-width: 960px) { .sidebar-section li { text-align: center; } }

.gallery { padding-top: 20px; }

@media only screen and (max-width: 960px) { .gallery { text-align: center; } }

.gallery__pic-frame { display: inline-block; background-color: #f3af9d; border: 1px solid #e8603c; }

.gallery--small .gallery__pic-frame { margin: 2px; height: 48px; width: 48px; }

.gallery--small img { width: 46px; height: 46px; }

footer .container-fluid { background-color: #f3af9d; }

footer .container { margin: 0 auto; padding: 0 10px; max-width: 960px; background-color: #fdfdfd; }

.info { display: flex; flex-direction: row; padding: 35px; background-color: #e8603c; }

.info h4 { padding-bottom: 15px; color: #fff; font-family: "BebasBold", sans-serif; font-size: 24px; font-weight: 700; font-style: bold; }

@media only screen and (max-width: 480px) { .info { flex-direction: column; } }

.info__about { max-width: 281px; font-family: "HelveticaNeueRoman", sans-serif; font-size: 13px; font-weight: 400; font-style: normal; color: #fff; text-align: left; }

.info__about p { margin-bottom: 12px; line-height: 1.25em; }

.info__twitter { padding-left: 60px; }

@media only screen and (max-width: 480px) { .info__twitter { margin-top: 30px; padding-left: 0; } }

.tweet { padding-bottom: 15px; max-width: 212px; font-family: "HelveticaNeueRoman", sans-serif; font-size: 13px; font-weight: 400; font-style: normal; color: #fff; text-align: left; line-height: 1.25em; }

.tweet__auth, .tweet__date { color: #f3af9d; cursor: pointer; text-decoration: none; }

.tweet__auth:hover, .tweet__date:hover { text-decoration: underline; }

.tweet__date { margin-top: 5px; }
